.wrapper {
    background-image: url('https://thewritelife.com/wp-content/uploads/2016/01/road-1031702_1920.jpg');
    background-position: center;
    background-repeat: no-repeat;
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    padding-bottom: 10px;
  }
.title {
    width: 100%;
    text-align: right;
    font-size: 24px;
    color: white;
    font-style: bold;
  }
.card {
  
    width: 160px;
    height: 160px;
    margin: 10px;
    position: relative;
    transition: all .23 ease-in-out;
  }
  
  .card > .img-container {
    height: 100%;
    overflow: hidden;
  }
  
  .card > .img-container > img {
    height: 100%;
  }
  
  .card:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  
  .card:active {
    -webkit-transform: translate(1px, 1px);
            transform: translate(1px, 1px);
  }
.title {
    width: 100%;
    text-align: center;
  }


@media only screen and (min-device-width : 768px) and (max-device-width : 1024px) and (orientation : portrait) and (-webkit-min-device-pixel-ratio : 2) {
  /* Styles */
  .container {
    padding-left:150px;
  }
  }
  /* Desktops and laptops ----------- */
  @media only screen  and (min-width : 1224px) {
  /* Styles */
  .container {
    padding-left:150px;
  }
  }
  
  /* Large screens ----------- */
  @media only screen  and (min-width : 1824px) {
  /* Styles */
  .container {
    padding-left:150px;
  }
  }
  

 
* {
    box-sizing: border-box;
    font-family: 'Times New Roman', Times, serif;
  }
  
  html, body, #root {
    padding: 0;
    margin: 0;
    height: 100%;
  }
